<template>
    <div id="singleblog">
        <h2>{{blog.title}}</h2>
      <h5 v-for="langue in blog.categories">{{langue}}</h5>
      <article>{{blog.centent}}</article>
      <article>{{blog.author}}</article>
      <button @click="deleteSingleblog()">删除</button>
      <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
    import axios from "axios"

export default {
    

    name: 'Singleblog',
    data() {
        return {
            id:this.$route.params.id,
            blog: {}
        }
    },
    created() {
        axios.get("/"+this.id).then((result) => {
            // console.log(result);
            this.blog=result.data
        });
    },
    methods:{
        deleteSingleblog(){
            axios.delete("/"+this.id).then((result) => {
                this.$router.push({path:'/'})
           })
        }
    }
}
</script>

<style scoped>
    #singleblog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background:#420eff ;
        color: #fff;
    }
</style>