<template>
  <div id="show-blogs">
    <span>博客总览</span>



    <input type="text" placeholder="搜索" v-model="search">
    <div v-for="blog in filteredBlogs" :key="blog.id" class="single-blog">
      <router-link :to="'/blog/'+blog.id">
        <h2>{{ blog.title}}</h2>
      </router-link>
      <h5>{{blog.categories.toString()}}</h5>
      <article>{{blog.content | snippet}}</article>
      <article>{{blog.author}}</article>
      <button @click="deleteSingleblog(blog.id)">删除</button>
      <router-link :to="'/edit/'+blog.id">编辑</router-link>

    </div>
  </div>
</template>
  
<script>
import axios from 'axios';
export default {
  name: 'Showblogs',
  data() {
    return {
      blogs: [],
      search: ''
    }
  },
  created() {
    axios.get('http://localhost:3000/posts').then((result) => {
      console.log(result);
      this.blogs = result.data
    });
  },
  methods: {
    async deleteSingleblog(id) {
      const { data } = await axios.delete(`http://localhost:3000/posts/${id}`)
      axios.get().then((result) => {
        
        this.blogs = result.data.posts
      });
    }


  },
  computed: {
    // 实现搜索框搜索功能
    filteredBlogs: function () {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search)
      })
    }
  },

  // 单组件定义过滤器
  filters: {
    toUppercase(value) {
      return value.toUpperCase()
    },
    snippet(value) {
      if (value.length > 100) {
        return value.slice(0, 100) + '...'
      } else {
        return value
      }
    }
  },

}
</script>
  
  
<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}

.single-blog {
  box-sizing: border-box;
  background: #420eff;
  color: #fff;
  padding: 20px 30px;
  margin: 20px 0;
  border-radius: 20px;
  position: relative;
}


.single-blog a {
  text-decoration: none;
  color: #fff;
}

input {
  width: 100px;
  height: 30px;
  border-radius: 20px;
  padding: 3px 10px;
  border: 2px #420eff solid;
  transition: all 1s;
}

input:hover {
  width: 300px;
  height: 30px;
  border-radius: 20px;
  padding: 3px 10px;
  border: 2px #14006b solid;
}

h2 {
  margin: 0 200px 20px 0;
}

h1 {
  color: #868686;
  text-align: center;
}

span {
  position: relative;
  font-size: 30px;
  text-align: center;
  margin: 0 auto;
  font-weight: 900;
  width: 200px;
  display: block;
}

span::before,
span::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #fc2f70;
  transform-origin: center;
  transform: scaleY(0);
  transition: transform 0.5s ease;
}

span::before {
  left: -8px;
}

span::after {
  right: -8px;
}

span:hover::before,
span:hover::after {
  transform: scaleY(1);
}




.boom {
    background-color: #16a085;
    color: #fff;
    position: relative;
    z-index: 1;
}

.boom::before {
    content: "";
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border: 2px solid #16a085;
    border-radius: 20px;
    transform-origin: center;
}

.boom:hover::before {
    transform: scale(1.25);
    transition: all ease-out .5s;
    border: 1px solid #96f3e0;
    opacity: 0;
}
</style>
  