<template>
    <div id="add-blog">
        <h2>编辑博客</h2>
        <from v-if="!submmited">
            <label>博客标题</label>
            <input type="text" v-model="blog.title" required />
            <label>博客内容</label>
            <textarea v-model="blog.content"></textarea>

            <div id="checkboxes">
                <label for="">Html</label>
                <input type="checkbox" value="Html" v-model="blog.categories">

                <label for="">Css</label>
                <input type="checkbox" value="Css" v-model="blog.categories">

                <label for="">Axios</label>
                <input type="checkbox" value="Axios" v-model="blog.categories">

                <label for="">Vue</label>
                <input type="checkbox" value="Vue" v-model="blog.categories">
            </div>

            <label>作者：</label>
            <select v-model="blog.author">
                <option v-for="author in authors">
                    {{author}}
                </option>
            </select>
            <button v-on:click.prevent="post">编辑博客</button>
        </from>

        <div v-if="submmited">
            <h3>您的博客发送成功！</h3>
        </div>
        <div id="preview">
            <h3>博客总览</h3>
            <p>博客标题：{{blog.title}}</p>
            <p>博客内容：</p>
            <p>{{blog.content}}</p>
            <p>博客分类：</p>
            <ul>
                <li v-for="category in blog.categories">
                    {{category}}
                </li>
            </ul>
            <p>作者：{{blog.author}}</p>
        </div>
    </div>
</template>
  
<script>
    import axios from "axios"
export default {
    name:'Editblog',
    data() {
        return {
            id: this.$route.params.id,
            blog: {
                title: '',
                content: '',
                categories: [],
                author: "",
                id:''
            },
            authors: ['yangp', 'yuanx'],
            submmited: false
        }
    },
    methods: {
        fetchData() {
            axios.get("/" + this.id).then((result) => {
                this.blog = result.data
            });
        },
        post: function () {

            axios.put("/" + this.id,this.blog).then((result) => {
                this.submmited=true
            })


        }
    },
    created() {
        this.fetchData()
    }
}
</script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
    display: block;
    box-sizing: border-box;
}

#add-blog {
    margin: 20px auto;
    max-width: 800px;
    padding: 20px;
}

label {
    display: block;
    margin: 20px 0 10px;
}

input[tyoe="text"],
textarea,
select {
    display: block;
    width: 100%;
    padding: 8px;
}

textarea {
    height: 200px;
}

#checkboxes>label {
    display: inline-block;
    margin-top: 0;
}

#checkboxes>input {
    display: inline-block;
    margin-right: 10px;
}

button {
    z-index: 1;
    position: relative;
    font-size: inherit;
    font-family: inherit;
    color: white;
    padding: 0.5em 1em;
    outline: none;
    border: none;
    background-color: #420eff;
    overflow: hidden;
    transition: color 0.4s ease-in-out;
    margin-top: 20px;
    border-radius: 5px;
}

button::before {
    content: '';
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    background-color: #170055;
    transform-origin: center;
    transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 0);
    transition: transform 0.45s ease-in-out;
    color: #fff;
}

button:hover {
    cursor: pointer;
    color: #ffffff;
}

button:hover::before {
    transform: translate3d(-50%, -50%, 0) scale3d(15, 15, 15);
}

#preview {
    margin: 30px 0;
    padding: 10px 20px;
}

h3 {
    margin-top: 10px;
}
</style>
  